.menu {
  height: 100vh;
  background-color: $fontGrey;
  z-index: 1;
 

  &__content {
    display: flex;
    flex-direction: column;

    box-sizing: border-box;
    height: 100vh;
    padding: 34px 0;

    @media (min-height:320px) {
      padding: 5;
    }
  }

  &__list {
    margin: 0 0 48px;
    padding: 0;
    list-style: none;

    @media (max-height:375px) {
      margin-bottom: 5px;
    }
  }

  &__item {
    margin: 0 34px;
    text-align: right;
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0;
    }

    @media (max-height:375px) {
      margin-bottom: 0;
    }
  }

  &__link {
    display: inline-block;
    padding-bottom: 8px;
    border-bottom: 1px solid transparent;

    font-weight: bold;
    font-size: 22px;
    line-height: 22px;
    color: #fff;
    text-decoration: none;
    letter-spacing: 2px;
    text-transform: uppercase;

    transition: border-color 0.5s;

    &:hover {
      border-color: #fff;
    }
  }

}
